/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
input {
  outline: none;
  padding: .3em .7em;
  border: 1px solid gray;
}
button {
  outline: none;
  padding: .3em 1em;
  border: 1px solid gray;
  cursor: pointer;
}
.modal-container {
  background-color: rgba(33, 33, 33, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 1;
  z-index: 1000; }
  .modal-container .modal-body {
    background-color: #fff;
    padding: 30px;
    width: 80%;
    max-width: 300px;
    position: absolute;
    left: 50%;
    top: 48%;
    -webkit-transform: translate3d(-50%, -50%, 0);
            transform: translate3d(-50%, -50%, 0); }

@media only screen and (max-width: 414px) {
  .modal-container .modal-body {
    padding: 20px;
    max-width: 280px; } }

.App {
  text-align: center; }
  .App header {
    width: 100%;
    height: 50px;
    line-height: 50px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    border-bottom: 1px solid gray;
    box-sizing: border-box; }
    .App header h1 {
      width: 90%;
      max-width: 1024px;
      text-align: left;
      margin: 0 auto; }
  .App footer {
    height: 50px;
    padding: 5px 0;
    line-height: 20px;
    text-align: center;
    border-top: 1px solid gray;
    box-sizing: border-box;
    font-size: 14px; }

.content {
  padding-top: 50px;
  min-height: calc(100vh - 100px);
  display: flex;
  justify-content: center;
  align-items: center; }
  .content p {
    line-height: 1.5em;
    font-size: 1em;
    margin-bottom: 1.5em; }
  .content button {
    line-height: 1.8em;
    cursor: pointer; }
    .content button:hover {
      background-color: blue;
      color: white; }

.invite-content {
  padding: 20px 0; }
  .invite-content h2 {
    line-height: 2em;
    margin-bottom: 20px; }
  .invite-content p {
    line-height: 1.5em;
    margin-bottom: 20px;
    font-size: 14px; }
  .invite-content button {
    display: block;
    width: 100%;
    line-height: 1.8em; }

.invite-form {
  width: 100%; }
  .invite-form input {
    width: 100%;
    line-height: 1.8em;
    box-sizing: border-box; }
    .invite-form input.error {
      border-color: red; }
  .invite-form em {
    line-height: 1.5em;
    font-size: 14px;
    color: red; }
  .invite-form button:disabled {
    opacity: 0.4; }

@media only screen and (max-width: 414px) {
  .invite-content {
    padding: 12px 0; }
    .invite-content h2 {
      margin-bottom: 12px; }
    .invite-content p {
      margin-bottom: 12px; } }

